.formInput {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  margin: 10px;
}

.label {
  color: rgba(102, 102, 108, 0.77);
  margin-bottom: 5px;
}

.formTextarea {
  display: block;
  font-weight: 400;
  background: rgba(237, 237, 243, 0.77);
  border: 1px solid rgba(102, 102, 108, 0.77);
  border-radius: 0.25rem;
  font-family: inherit;
  font-size: 1rem;
  padding: 0.375rem 0.75rem;
}

.error {
  color: #8d3535;
}
