.formContainer {
  display: flex;
  min-width: 30%;
  flex-direction: column;
  padding: 20px;
  margin: 20px;
  flex-wrap: wrap;
  border: 1px solid #b6b6b6;
  border-radius: 10px;
  background-color: #f1f0f0;
}

.buttonBlock {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}
